import { Product } from '../components/Product/Product';
import css from '../components/App.module.css';
import Section from '../components/Section/Section';
import { useEffect, useState } from 'react';
import ProductForm from '../components/ProductForm/ProductForm';
import { nanoid } from 'nanoid';
import Modal from '../components/Modal/Modal';
import { useDispatch, useSelector } from 'react-redux';

const ProductsPage = () => {
  const dispatch = useDispatch();
  const [counterValue, setCounterValue] = useState(0);
  const [isOpenModal, setIsOpenModal] = useState(false);
  const [modalData, setModalData] = useState(null);

  const products = useSelector(state => state.productsStore.products);

  /////–ü–†–ò –û–ù–û–í–õ–ï–ù–ù–Ü –°–¢–û–†–Ü–ù–ö–ò –ü–ï–†–ï–í–Ü–†–ö–ê –ß–ò –ö–û–†–ò–°–¢–£–í–ê–ß –ù–û–í–ò–ô –¢–ê –©–û –ü–û–ö–ê–ó–£–í–ê–¢–ò. –ê–ë–û –ó–ê –ó–ê–ú–û–í–ß–£–í–ê–ù–ù–Ø–ú –ú–ê–°–ò–í (productsData) –ê–ë–û –ô–û–ì–û –ü–†–û–î–£–ö–¢–ò/////
  // const [products, setProducts] = useState(() => {
  //   const stringifiedProducts = localStorage.getItem('products');
  //   const parsedProducts = JSON.parse(stringifiedProducts) ?? productsData;
  //   return parsedProducts;
  // });

  ///–ü–†–ò –ó–ú–Ü–ù–Ü –ú–ê–°–ò–í–£ –ü–†–û–î–£–ö–¢–Ü–í –ô–î–ï –ó–ê–ü–ò–° –£ –õ–û–ö–ê–õ–¨–ù–ï –°–•–û–í–ò–©–ï/////
  useEffect(() => {
    const stringifiedProducts = JSON.stringify(products);
    localStorage.setItem('products', stringifiedProducts);
  }, [products]);

  //////–ó–ú–ï–ù–®–ï–ù–ù–Ø –õ–Ü–ß–ò–õ–¨–ù–ò–ö–ê//////
  const handleDecrement = () => {
    if (counterValue === 0) {
      alert('Counter value is already decremented!');
      return;
    }
    setCounterValue(prevState => prevState - 1);
    //OR//
    // this.setState({ counterValue: this.state.counterValue - 1 });
  };

  //////–ó–ë–Ü–õ–¨–®–ï–ù–ù–Ø –õ–Ü–ß–ò–õ–¨–ù–ò–ö–ê//////
  const handleIncrement = () => {
    setCounterValue(prevState => prevState + 1);
    //OR//
    // this.setState({ counterValue: this.state.counterValue + 1 });
  };

  //////–í–ò–î–ê–õ–ï–ù–ù–Ø –ü–†–û–î–£–ö–¢–£//////
  const handleDeleteProduct = productId => {
    const deleteProductAction = {
      type: 'products/deleteProduct',
      payload: productId,
    };

    dispatch(deleteProductAction);
  };
  //////–î–û–î–ê–í–ê–ù–ù–Ø –ü–†–û–î–£–ö–¢–£//////
  const handleAddProduct = productData => {
    const hasDuplicates = products.some(
      product => product.title === productData.title
    );

    if (hasDuplicates) {
      alert(`Oops! Product with title '${productData.title}' already exists`);
      return;
    }

    const finalProduct = {
      ...productData,
      id: nanoid(),
    };

    const addProductAction = {
      type: 'products/addProduct',
      payload: finalProduct,
    };
    dispatch(addProductAction);
  };

  //////–í–Ü–î–ö–†–ò–¢–¢–Ø –ú–û–î–ê–õ–ö–ò//////
  const openModal = someDataToModal => {
    setIsOpenModal(true);
    setModalData(someDataToModal);
  };

  //////–ó–ê–ö–†–ò–¢–¢–Ø –ú–û–î–ê–õ–ö–ò//////
  const closeModal = () => {
    setIsOpenModal(false);
    setModalData(null);
  };

  //–°–û–†–¢–£–í–ê–ù–ù–Ø –ü–†–û–î–£–ö–¢–Ü–í: –ü–ï–†–®–Ü –ó–Ü –ó–ù–ò–ñ–ö–û–Æ –ô–î–£–¢–¨//
  const sortedProducts = [...products].sort((a, b) => b.discount - a.discount);
  //////////////
  return (
    <div>
      <Section title="Hello and counter">
        <h1>Hello</h1>
        {counterValue >= 5 && (
          <h3>
            üéâ Congrats! You won the discount promocode 30% OFF #DW13S4RE7 üéä
          </h3>
        )}
        <button onClick={handleDecrement}>Decrement</button>
        <b>Counter Value: {counterValue}</b>
        <button onClick={handleIncrement}>Increment</button>
      </Section>

      <Section title="Product Form">
        <ProductForm handleAddProduct={handleAddProduct} />
      </Section>

      <Section title="Product List">
        <div className={css.productList}>
          {sortedProducts.map(product => {
            return (
              <Product
                key={product.id}
                title={product.title}
                id={product.id}
                price={product.price}
                discount={product.discount}
                handleDeleteProduct={handleDeleteProduct}
                openModal={openModal}
              />
            );
          })}
        </div>
      </Section>
      {isOpenModal && (
        <Modal closeModal={closeModal} modalData={modalData}></Modal>
      )}
    </div>
  );
};

export default ProductsPage;
