import React, { useEffect, useState } from 'react';
import { StyledModal } from './Modal.Styled';

// –ú–µ—Ç–æ–¥–∏ –∂–∏—Ç—Ç—î–≤–æ–≥–æ —Ü–∏–∫–ª—É - —Ü–µ –∑–∞—Ä–µ–∑–µ—Ä–≤–æ–≤–∞–Ω—ñ —Ä–µ–∞–∫—Ç–æ–º –º–µ—Ç–æ–¥–∏(—Ñ—É–Ω–∫—Ü—ñ—ó),
//  —è–∫—ñ –∑–∞–ø—É—Å–∫–∞—é—Ç—å—Å—è –≤ –ø–µ–≤–Ω–∏–π –ø–µ—Ä—ñ–æ–¥ –∂–∏—Ç—Ç—è –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∏ —Å–∞–º–∏–º –†–µ–∞–∫—Ç–æ–º.

//  componentDidMount() {} - –º–µ—Ç–æ–¥ –∂–∏—Ç—Ç—î–≤–æ–≥–æ —Ü–∏–∫–ª,
//     —â–æ –∑–∞–ø—É—Å–∫–∞—î—Ç—å—Å—è –æ–¥–∏–Ω —Ä–∞–∑, –ø—ñ—Å–ª—è —É—Å–ø—ñ—à–Ω–æ–≥–æ –º–æ–Ω—Ç—É–≤–∞–Ω–Ω—è –∫–æ–º–ø–æ–Ω–µ—Ç–∏ –≤ DOM.

//     –í–∏–∫–æ—Ä–∏—Å—Ç–∞–Ω–Ω—è:
//     - –í—ñ—à–∞—é—Ç—å—Å—è –≥–ª–æ–±–∞–ª—å–Ω—ñ —Å–ª—É—Ö–∞—á—ñ –ø–æ–¥—ñ–π (addEventListener)
//     - –í—Å—Ç–∞–Ω–æ–≤–ª—é—é—Ç—å—Å—è –∞—Å–∏–Ω—Ö—Ä–æ–Ω–Ω—ñ —Ç–∞–π–º–µ—Ä–∏ —Ç–∞ –ª—ñ—á–∏–ª—å–Ω–∏–∫–∏ (setTimeout, setInterval)
//     - –ó—á–∏—Ç—É—é—Ç—å—Å—è –¥–∞–Ω—ñ –∑ –ª–æ–∫–∞–ª—å–Ω–æ–≥–æ —Å—Ö–æ–≤–∏—â–∞ —Ç–∞ –≤—Å—Ç–∞–Ω–æ–≤–ª—é—î–º–æ —ó—Ö –≤ —Å—Ç–µ–π—Ç
//     - –ù–∞–¥—Å–∏–ª–∞—é—Ç—å—Å—è –º–µ—Ä–µ–∂–µ–≤—ñ –∑–∞–ø–∏—Ç–∏ (HTTP request)

//  componentWillUnmount() {} - –º–µ—Ç–æ–¥ –∂–∏—Ç—Ç—î–≤–æ–≥–æ —Ü–∏–∫–ª,
//     —â–æ –∑–∞–ø—É—Å–∫–∞—î—Ç—å—Å—è –æ–¥–∏–Ω —Ä–∞–∑, –ø–µ—Ä–µ–¥ –ø–æ–≤–Ω–∏–º –≤–∏–¥–∞–ª–µ–Ω–Ω—è–º –∫–æ–º–ø–æ–Ω–µ—Ç–∏ –∑ DOM.

//     –í–∏–∫–æ—Ä–∏—Å—Ç–∞–Ω–Ω—è:
//     - –ü—Ä–∏–±–∏—Ä–∞—é—Ç—å—Å—è –≥–ª–æ–±–∞–ª—å–Ω—ñ —Å–ª—É—Ö–∞—á—ñ –ø–æ–¥—ñ–π (removeEventListener)
//     - –ü—Ä–∏–±–∏—Ä–∞—Ç–∏ –∞—Å–∏–Ω—Ö—Ä–æ–Ω–Ω—ñ —Ç–∞–π–º–µ—Ä–∏ —Ç–∞ –ª—ñ—á–∏–ª—å–Ω–∏–∫–∏ (clearTimeout, clearInterval)
//     - –í—ñ–¥—Ö–∏–ª—è—Ç–∏ –º–µ—Ä–µ–∂–µ–≤—ñ –∑–∞–ø–∏—Ç–∏ (cancel HTTP request)

//  componentDidUpdate(prevProps, prevState) {} - –º–µ—Ç–æ–¥ –∂–∏—Ç—Ç—î–≤–æ–≥–æ —Ü–∏–∫–ª,
//     —â–æ –∑–∞–ø—É—Å–∫–∞—î—Ç—å—Å—è –∫–æ–∂–µ–Ω —Ä–∞–∑, –ø—ñ—Å–ª—è —Ç–æ–≥–æ, —è–∫ –∫–æ–º–ø–æ–Ω–µ–Ω—Ç–∞ –æ–Ω–æ–≤–∏–ª–∞—Å—è(–∑–º—ñ–Ω–∏–ª–∏—Å—è –ø—Ä–æ–ø—Å–∏, –∞–±–æ —Å—Ç–µ–π—Ç).

//     –í–∏–∫–æ—Ä–∏—Å—Ç–∞–Ω–Ω—è:
//     - –ù–∞–¥—Å–∏–ª–∞—é—Ç—å—Å—è –º–µ—Ä–µ–∂–µ–≤—ñ –∑–∞–ø–∏—Ç–∏ (HTTP request)
//     - –û–Ω–æ–≤–ª—é—é—Ç—å(—Å–∏–Ω—Ö—Ä–æ–Ω—ñ–∑—É—é—Ç—å—Å—è) –¥–∞–Ω—ñ –∑—ñ —Å—Ç–µ–π—Ç—É –∑ –ª–æ–∫–∞–ª—å–Ω–∏–º —Å—Ö–æ–≤–∏—â–µ–º

const Modal = ({ modalData, closeModal }) => {
  // state = {
  //   counter: 1,
  // };
  const [counter, setCounter] = useState(1);

  // componentDidMount()
  useEffect(() => {
    const handleKeyDown = evt => {
      if (evt.code === 'Escape') {
        closeModal();
      }
    };

    window.addEventListener('keydown', handleKeyDown);
    document.body.style.overflow = 'hidden';

    return () => {
      // componentWillUnmount(
      window.removeEventListener('keydown', handleKeyDown);
      document.body.style.overflow = 'auto';
    };
  }, [closeModal]);

  useEffect(() => {
    console.log('counter:' + counter);
  }, [counter]);
  // componentDidMount() {
  //   // console.log('Modal has successfully been mounted');
  //   document.body.style.overflow = 'hidden';
  //   window.addEventListener('keydown', this.handleKeyDown);
  // }

  // componentWillUnmount() {
  //   // console.log('Modal was successfully deleted');
  //   window.removeEventListener('keydown', this.handleKeyDown);
  //   document.body.style.overflow = 'auto';
  // }

  const handleIncrementProduct = () => {
    // setCounter(counter + 1);
    /////–ê–ë–û/////
    setCounter(prevState => prevState + 1);
  };

  const handleOverlayClick = evt => {
    if (evt.target === evt.currentTarget) {
      closeModal();
    }
  };

  return (
    <StyledModal onClick={handleOverlayClick}>
      <div className="modal">
        <button onClick={closeModal} type="button" className="closeBtn">
          &times;
        </button>
        <h2>Product details</h2>
        <div>
          <h3>Title: {modalData.title}</h3>
          <p>Price: {modalData.price}$</p>
          <p>Discount: {modalData.discount}$</p>
          <button onClick={handleIncrementProduct}>
            Add product: {counter}
          </button>
        </div>
      </div>
    </StyledModal>
  );
};

export default Modal;
